.is-hidden {
  display: none;
}

.eb-typed-add-wrapper {
  margin: 0 auto;
}

.eb-typed-sortable-item {
  display: -ms-flexbox;
  display: flex;
  justify-content: space-between;
  margin: 10px 0px;
  border: 1px solid;
  line-height: 2.5em;
}

.eb-typed-sortable-title {
  cursor: pointer;
  flex: 12;
  padding-left: 5px;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 200px;
  white-space: nowrap;
  align-self: center;
}

.eb-typed-input-wrapper {
  display: -ms-flexbox;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  margin-top: 10px;
}

.eb-typed-sortable-trash {
  font-size: 16px;
  border-left: 1px solid gray;
  flex: 1;
  text-align: center;
  padding: 8px;
}

.eb-typed-sortable-trash:hover {
  background: #f44336;
  color: #ffffff;
  cursor: pointer;
}

.st0t {
  fill: url(#SVGID_1_TYPED);
}

.st1t {
  fill: url(#SVGID_2_TYPED);
}

.st2t {
  fill: url(#SVGID_3_TYPED);
}

.eb-typography-base div {
  display: flex;
  justify-content: space-between;
}

.drag-handle {
  flex: 1;
  background: darkgrey;
  border-right: 1px solid;
  cursor: move;
  color: #ffffff;
  align-items: center;
  display: flex !important;
  justify-content: center;
}

.drag-helper {
  z-index: 100;
  list-style: none;
}